  font-family: Raleway;
  position: relative;
}

/* Billboard */

.billboard {
  margin: 0px;
  background-attachment: fixed;
  background-image: url(./images/desk.jpg);
  background-position: center;
  background-size: cover;
  background-repeat: no-repeat;
  color: white;
  height: 670px;
  z-index: -100;
  -webkit-transition: 0s linear;
  -o-transition: 0s linear;
  transition: 0s linear;
  background-position: 0 -100px;
}

/* Menu */

.menu {
  display: -ms-flexbox;
  display: flex;
  margin: auto 15px;
  font-size: 1.2em;
  }

.logo {
  font-size: 1.3em;
  margin-right: 15px;
}

.links {
  display: -ms-flexbox;
  display: flex;
  margin: auto 0 auto auto;
}

.links div {
  padding-right: 15px;
  -ms-flex-align: end;
      align-items: flex-end;
}

.links a {
  text-decoration: none;
  color: inherit;
}

.active {
  font-weight: bold;
}

